body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
	font-family: "vetPoint_font";
	src: local("MyFont"),
		url(/vetPoint_Website/static/media/amsterdam-three-slant.7839e7a4.ttf) format("truetype");
	/* other formats include: 'woff2', 'truetype, 'opentype',
                              'embedded-opentype', and 'svg' */
}

ul {
	list-style-type: none;
}

header {
	width: 100%;
}

nav {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	flex-direction: row;
	grid-column-gap: 1rem;
	-webkit-column-gap: 1rem;
	        column-gap: 1rem;
	width: calc(100% - 2% - 1rem);
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	align-items: center;
	text-align: center;
	padding: 0.5%;
}

nav img {
	display: block;
	max-width: 150px;
	max-height: 150px;
}

nav .leftSide {
	display: flex;
	justify-content: space-evenly;
	align-items: flex-start;
	text-align: center;
}

nav .vetPoint_logoName {
	font-size: 1.8rem;
	font-family: vetPoint_font, Verdana, Geneva, Tahoma, sans-serif;
	color: #6a1c1b;
}

nav .lower_logoName {
	font-size: 1.2rem;
}
nav .middleSide ul {
	display: flex;
	justify-content: space-evenly;
	flex-wrap: wrap;
	grid-column-gap: 2rem;
	-webkit-column-gap: 2rem;
	        column-gap: 2rem;
	list-style: none;
	padding: 0;
}

nav .middleSide li {
	float: left;
	font-size: 1.5rem;
}

nav .rightSide {
	display: flex;
	justify-content: space-evenly;
	text-align: center;
	flex-wrap: wrap;
	grid-column-gap: 1rem;
	-webkit-column-gap: 1rem;
	        column-gap: 1rem;
}
nav .rightSide button,
.lowerHeader .welcome button {
	background-color: #6a1c1b;
	color: #fff;
	padding: 2% 3%;
	border: none;
	font-size: 1rem;
	width: 200px;
	height: 35px;
}

