	background-color: #6a1c1b;
	width: calc(100% - 2%);
	padding: 1%;
	height: calc((30px - 2%));
	color: #fff;
	font-size: 1.2rem;
} */

.lowerHeader {
	width: 100%;
}

.lowerHeader .banner {
	width: calc(100%);
	height: calc(100vh - 100px);
	background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;

	display: flex;

	align-content: center;
	overflow-y: hidden;
}

.lowerHeader .welcome {
	display: flex;
	align-content: flex-start;
	justify-content: center;
	flex-direction: column;
	text-align: justify;
	width: calc(100% - 4%);
	height: 100%;
	padding: 2%;
	background-color: rgba(0, 0, 0, 0.3);
	color: #fff;
	text-shadow: 1px 1px 0px #0000;
}

.lowerHeader .welcome h1 {
	margin: 0;
}
.lowerHeader .welcome span:first-of-type {
	font-size: 1.2rem;
	margin: 2% 0;
}

.lowerHeader .welcome button {
	color: #6a1c1b;
	background-color: #fff;
	box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	padding: 1%;
	margin: 2% 1%;
	margin-left: 0;
}

.pageTitle {
	display: flex;
	flex-wrap: wrap;
	justify-content: left;
	width: calc(100% - 2%);
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	padding: 1%;
	background-color: #6a1c1b;
	color: #fff;
	text-align: center;
}

.pageTitle h3 {
	text-align: left;
	margin: 1px 0;
}

/* features start */

.features {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	flex-direction: column;
	width: calc(100%);
	padding: 4% 0;
	color: #000;
}

.features .featureHeader hr {
	width: calc(50px - 2%);
	height: calc(50px - 2%);
	padding: 1%;
	border: none;
	margin: 0 auto;
	border-top: solid 4px #6a1c1b;
}

.features .featureHeader b {
	display: block;
	width: calc(100% - 2%);
	text-align: center;
	margin: 0 1% 1% 1%;
	font-size: 1.3rem;
	text-transform: uppercase;
}

.features .featuresColumnWrapper {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	flex-direction: row;
	width: calc(100% - 4%);
	padding: 2%;
	color: #000;
}
.information {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: center;
	align-content: center;
	text-align: left;
}
.information b {
	display: block;
	width: calc(100% - 2%);
	text-align: left;
	margin: 5% 1% 1% 1%;
	font-size: 1.3rem;
	text-transform: uppercase;
}
.information .informationDetails {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: center;
	align-content: center;
	overflow-wrap: break-word;
}
.information .informationDetails section {
	display: flex;
	flex-direction: row;
	align-items: center;
	background-color: #fff;
	grid-column-gap: 10px;
	-webkit-column-gap: 10px;
	        column-gap: 10px;
	margin: 1% 0;
}
.information .informationDetails span.bullets {
	display: inline-flex;
	background-color: #6a1c1b;
	color: #fff;
	box-shadow: 16px 8px 15px rgba(0, 0, 0, 0.1);
	width: 40px;
	height: 40px;
	font-size: 25px;
	text-align: center;
	justify-content: center;
	place-items: center;
}

.posters {
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	padding: 1%;
	border: 1px solid #e1e1e6;
	border-radius: 25px;
	box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
}

/* features end */

/* services start */
.service {
	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 100%;

	font-size: 1.2rem;
}

.service .clinicServices {
	width: calc(100% - 2%);
	height: calc(100% - 2%);
	padding: 1%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	grid-column-gap: 5rem;
	-webkit-column-gap: 5rem;
	        column-gap: 5rem;
	text-align: center;
}
.service .clinicServices .serviceHeader {
	width: calc(100% - 2%);
	height: calc(50px - 2%);
	padding: 1%;
	text-align: center;
}

.service .clinicServices .serviceHeader hr {
	width: calc(50px - 2%);
	height: calc(50px - 2%);
	padding: 1%;
	border: none;
	margin: 0 auto;
	border-top: solid 4px #6a1c1b;
}

.service .clinicServices .serviceHeader b {
	display: block;
	width: calc(100% - 2%);
	text-align: center;
	margin: 0 1% 1% 1%;
	font-size: 1.3rem;
	text-transform: uppercase;
}

.service .clinicServices .serviceOffer {
	width: 200px;
	height: 200px;
	box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);

	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	border-bottom: 2px solid rgba(161, 143, 143, 0.533);
}

.service .clinicServices .nameWrapper {
	background-color: #fff;
	box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
	padding: 2%;
}

/* services end */

/* about start */

.about {
	width: 100%;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	margin: 3% 0 0 0;
}

.about .aboutHeader hr {
	width: calc(50px - 2%);
	height: calc(50px - 2%);
	padding: 1%;
	border: none;
	margin: 0 auto;
	border-top: solid 4px #6a1c1b;
}

.about .aboutHeader b {
	display: block;
	width: calc(100% - 2%);
	text-align: center;
	margin: 0 1% 1% 1%;
	font-size: 1.3rem;
	text-transform: uppercase;
}

.about .imgAbout {
	display: flex;
	justify-content: center;
	width: 100%;
	height: 100%;
}

.about .imgAbout .imgPart {
	width: 75%;
	height: calc(100%);
	background-size: contain;
	background-repeat: no-repeat;
}

.about .imgAbout .imgPart img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.about .infoAbout {
	float: right;
	width: calc(50% - 4%);
	height: calc(100% - 4%);

	padding: 2%;

	font-size: 1.2rem;
	color: #000;
}

/* about end */
/* footer start */
footer {
	width: calc(100% - 2%);
	padding: 1%;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	display: flex;
	justify-content: space-evenly;
	flex-direction: row;
	flex-wrap: wrap;
	align-items: center;
	color: #fff;
	background-color: #6a1c1b;
}

footer .leftSideFooter {
	display: flex;
	flex-direction: column;
	justify-content: start;
	align-items: center;
	width: calc(50%);
	height: 100%;
	text-align: center;
}

footer .leftSideFooter .footerLogo,
footer .leftSideFooter span {
	width: 100%;
	height: 100%;
}
footer img {
	max-height: 100px;
	max-width: 100px;
	margin-right: 0;
	object-fit: cover;
}

footer .lowerFooter {
	margin-top: 5px;
	display: flex;
	flex-direction: column;
	text-align: center;
	text-indent: 5px;
}

/* footer end */

/* buttons */
button:hover {
	cursor: pointer;
}

.mapouter {
	position: relative;
	text-align: right;
	width: 100%;
	height: 400px;
	box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
}
.gmap_canvas {
	overflow: hidden;
	background: none !important;
	width: 100%;
	height: 400px;
}
.gmap_iframe {
	height: 400px !important;
}

.loginWrapper {
	width: calc(100%);
	height: calc(100vh);
	background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;

	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row;
	overflow: hidden;
}

.loginForm {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
}
.login_webName {
	text-align: center;
	color: #6a1c1b;
}
.login_webName b {
	font-size: 5rem;
}

.login_webName span b {
	font-size: 1.5rem;
}

.login_logo {
	position: relative;
	width: 150px;
	height: 100px;
	z-index: 1;
}

.login_logo img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.loginFormWrapper {
	position: relative;
	z-index: 0;
	background-color: #6a1c1b;
	width: calc(350px - 6%);
	height: 300px;
	padding: 3%;
	text-align: left;
	color: #fff;
	border-radius: 25px;
}

.loginFormWrapper b {
	display: block;
	width: calc(100% - 10%);
	padding: 2% 5%;
	font-size: 1.2rem;
}

.loginForm form {
	width: calc(100% - 6%);
	padding: 3%;
}

.loginForm form input {
	border: 1px solid #e1e1e6;
	width: calc(100% - 10px - 2%);
