}

.loginForm form button {
	width: calc(100% - 2%);
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	padding: 3% 1%;
	margin: 3% 1%;
	background-color: rgba(184, 101, 95, 255);
	color: #fff;
	border: none;
	font-size: 1rem;
}

.loginWrapper .cat_bg,
.loginWrapper .dog_bg {
	width: 300px;
	height: 100vh;
	background-position: bottom;
	background-size: contain;
	background-repeat: no-repeat;
}

.appointmentFormWrapper {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 50%;
}

h3 {
	text-align: center;
}

.appointmentForm {
	display: flex;
	justify-content: center;
	width: calc(700px - 2%);
	padding: 1%;
}

.appointmentForm form {
	width: calc(100% - 6%);
	padding: 3%;
}

.appointmentForm form div {
	display: flex;
	flex-wrap: wrap;
	justify-content: left;
	width: calc(100% - 2%);
	padding: 1%;
	grid-column-gap: 2rem;
	-webkit-column-gap: 2rem;
	        column-gap: 2rem;
}

.appointmentForm form span {
	display: block;
	max-width: inherit;
	width: 250px;
}

.appointmentForm form input {
	border: 1px solid #e1e1e6;
	width: 100%;
	height: 40px;
	padding-left: 10px;
	border-radius: 10px;
}

.appointmentForm form input[type="checkbox"] {
	display: inline;
	width: 20px;
	vertical-align: middle;
}

.appointmentForm form select {
	width: 100%;
	height: 40px;
	padding-left: 10px;
}

.appointmentForm form ul {
	padding-left: 0;
}

/* buttons */
.buttonWrapper {
	width: calc(100% - 4%);
	padding: 2%;
}

.buttonWrapper button {
	color: #fff;
	background-color: #008000;
	width: calc(100% - 2%);
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.1);
	padding: 1%;
	border: none;
	border-radius: 10px;
}

/* buttons */
.appointmentSchedWrapper {
	width: calc(100% - 2px - 10%);
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	border: 1px solid #e1e1e6;
	border-radius: 10px;
	margin: 2% 5%;
}

/* schedule start */
.scheduleWrapper {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	width: calc(100%);
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	padding: 5% 0;
	margin: 0;
}

.react-calendar {
	border: 1px solid #e1e1e6;
	border-radius: 10px;
	border-right: none;
	border-top-right-radius: 0;
	border-bottom-right-radius: 0;
	box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
}

.react-calendar abbr[title] {
	text-decoration: none;
}

.scheduleDetails {
	width: 500px;
	max-width: 100%;
	height: 300px;
	text-align: justify;
	border: 1px solid #e1e1e6;
	border-radius: 10px;
	border-top-left-radius: 0;
	border-bottom-left-radius: 0;
	border-left: none;
}

@media only screen and (max-width: 800px) {
	.react-calendar {
		width: 100%;
		border: none;
		box-shadow: none;
	}

	.scheduleDetails {
		width: 500px;
		max-width: 100%;
		height: 300px;
		text-align: justify;
		border: none;
	}
}

.scheduleDetailsHeader {
	display: flex;
	justify-content: space-evenly;
	width: calc(100% - 2%);
	padding: 1%;
}

.scheduleDetails ul {
	text-align: left;
}

/* legend start */
.legend {
	width: 200px;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	font-size: small;
}
.legend ul {
	display: inline-block;
	width: 100%;
	text-align: left;
}
.legend li {
	width: 100%;
}

.legend .yellow,
.legend .blue,
.legend .gray {
	display: inline-block;
	width: 15px;
	height: 15px;
	background-color: #ffff76;
	vertical-align: middle;
}

.legend .blue {
	background-color: #1087ff;
}

.legend .gray {
	background-color: #e6e6e6;
}
/* legend end */

.faeyeslash:hover {
	cursor: pointer;
}

/* schedule end*/

* {
	font-family: Arial, Helvetica, sans-serif;
}

a {
	text-decoration: none;
	color: initial;
}

.smallDetails {
	font-size: smaller;
	color: #888;
	page-break-after: auto;
	-webkit-column-break-after: auto;
	        break-after: auto;
}

.fade {
	-webkit-animation-name: fade;
	        animation-name: fade;
	-webkit-animation-duration: 1.5s;
	        animation-duration: 1.5s;
}

@-webkit-keyframes fade {
	from {
		opacity: 0.4;
	}
	to {
		opacity: 1;
	}
}

@keyframes fade {
	from {
		opacity: 0.4;
	}
	to {
		opacity: 1;
	}
}

